.itemcontainer {
    height: fit-content;
    min-height: 200px;
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-top: 1px solid rgba(0, 0, 0, 0.169);
}
.imagecontainer {
    min-width: 80px;
    max-width: 80px;
    min-height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all;
    transition-duration: 500ms;
}
.imagecontainerModal {
    border-radius: 25px;
    background-color: white;
    padding: 20px;
    width: 70vw;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all;
    transition-duration: 500ms;
}

.imagecontainer:hover {
    transform: scale(1.1);
}

.image {
    max-width: 100%;
    max-height: 100%;
}
.descriptions {
    display: flex;
    flex-direction: row;
    max-width: 70%;
    align-items: center;
}
.description {
    margin: 0;
    padding: 5px 10px;
    font-family: "Open Sans", sans-serif;
    font-weight: 300;
    line-height: 120%;
    font-size: 14px;
    color: #555;
}
.producttext {
    padding: 0 10px;
}

.price {
    display: inline-block;
    font-family: "Fjalla One", sans-serif;
    color: #252525;
    font-size: 26px;
}

.rightitems {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}
.deletebutton {
    display: inline-block;
    background: white;
    border: white;
    cursor: pointer;
    color: #96007d;
    font-size: 18px;
    font-family: "Open Sans", sans-serif;
}

.deleteItem {
    transition: cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition-duration: 500ms;
    opacity: 0;
    transform: scale(0, 0);
}

.title {
    padding: 5px 10px;
    font-family: "Montserrat", sans-serif;
    font-weight: 800;
}

@media (max-width: 768px) {
    .description {
        display: none;
    }
    .title {
        font-weight: 300;
    }
}
.editIcon {
    opacity: 0.5;
}

.editIcon:hover {
    opacity: 1;
}
