@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500&family=Montserrat:wght@100;200;500;600;700;800;900&display=swap");
.logo {
    font-family: "Comfortaa", cursive;
    display: flex;
    flex-direction: row;
    font-size: large;
    font-weight: 700;
    animation-name: logo_neon;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

@keyframes logo_neon {
    0% {
        text-shadow: 0 0 7px rgb(157, 0, 255), 0 0 10px rgb(157, 0, 255), 0 0 21px rgb(132, 0, 255),
            0 0 42px rgb(251, 0, 255), 0 0 82px rgb(251, 0, 255);
    }
    100% {
        text-shadow: 0 0 7px rgb(25, 0, 255), 0 0 10px rgb(25, 0, 255), 0 0 21px rgb(25, 0, 255),
            0 0 42px rgb(0, 234, 255), 0 0 82px rgb(0, 234, 255);
    }
}

.s {
    color: white;
    animation-name: o_animation;
    animation-duration: 10s;
    animation-direction: infinite;
    animation-iteration-count: infinite;
    animation-delay: 1.1s;
    transition: all;
    transition: cubic-bezier(1, -0.19, 0.03, 2.29);
}

.h {
    color: white;
    animation-name: o_animation;
    animation-duration: 10s;
    animation-direction: normal;
    animation-iteration-count: infinite;
    animation-delay: 1.3s;
    transition: all;
    transition: cubic-bezier(1, -0.19, 0.03, 2.29);
}
.o {
    color: white;
    animation-name: o_animation;
    animation-duration: 10s;
    animation-direction: normal;
    animation-iteration-count: infinite;
    animation-delay: 1.3s;
    transition: all;
    transition: cubic-bezier(1, -0.19, 0.03, 2.29);
}
.p {
    color: white;
    animation-name: o_animation;
    animation-duration: 10s;
    animation-direction: normal;
    animation-iteration-count: infinite;
    animation-delay: 1.2s;
    transition: all;
    transition: cubic-bezier(1, -0.19, 0.03, 2.29);
}
@keyframes o_animation {
    97% {
        color: white;
        scale: (1, 1);
    }
    98% {
        text-shadow: 0 0 7px rgb(25, 0, 255), 0 0 10px rgb(25, 0, 255), 0 0 21px rgb(25, 0, 255),
            0 0 42px rgb(0, 234, 255), 0 0 82px rgb(0, 234, 255);
        color: rgb(17, 49, 255);
        scale: (1, 1);
    }
    99% {
        text-shadow: 0 0 7px rgb(249, 0, 0), 0 0 10px rgb(255, 29, 29), 0 0 21px rgb(255, 56, 99),
            0 0 42px rgb(255, 0, 111), 0 0 82px rgb(255, 255, 255);
    }
    100% {
        text-shadow: 0 0 7px rgb(255, 255, 255), 0 0 10px rgb(255, 255, 255), 0 0 21px rgb(255, 255, 255),
            0 0 42px rgb(255, 255, 255), 0 0 82px rgb(255, 255, 255);
        scale: (1.3, 1.1);
    }
}
