.iconContainer {
    position: relative;
    cursor: pointer;
}
.modal {
    position: fixed;
    left: -360px;
    top: 50px;
    max-height: 70vh;
    overflow: hidden;
    background: rgb(255, 255, 255);
    border-radius: 10px;
    object-fit: contain;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.25);
    animation-name: anim;
    animation-duration: 0.5s;
    animation-iteration-count: 1;
}
.scaleContainer {
    zoom: 0.5;
    display: flex;
    justify-content: center;
    align-items: center;
}

@keyframes anim {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 0.9;
    }
}
.card_icon {
    height: 25px;
}

.count {
    position: absolute;
    left: 14px;
    top: 0;
    display: inline-block;
    width: 15px;
    height: 15px;
    background: orange;
    border: 1px orange solid;
    border-radius: 50%;
    text-align: center;
    justify-content: center;
    font-size: 12px;
}
